import React, { useEffect, useState } from 'react';
import { supabase } from '../supabaseClient';

export default function PaymentModal({ row, onClose, onDone }) {
  const [amount, setAmount] = useState(0);
  const [method, setMethod] = useState(row?.payment_method || 'cash');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setAmount(Number(row?.remaining_amount || 0));
    setMethod(row?.payment_method || 'cash');
  }, [row]);

  const submit = async () => {
    setLoading(true);
    try {
      const paid = Number(row?.amount_paid || 0) + Number(amount || 0);
      const { error } = await supabase.from('reservations').update({ amount_paid: paid, payment_method: method }).eq('id', row.id);
      if (error) throw error;
      onDone && onDone();
      onClose && onClose();
    } catch(e) {
      alert('  : '+(e.message||e));
    } finally { setLoading(false); }
  };

  return (
    <div className="fixed inset-0 bg-black/30 flex items-end md:items-center justify-center p-4" dir="rtl">
      <div className="bg-white w-full md:max-w-md rounded-lg shadow-lg overflow-hidden">
        <div className="px-4 py-3 border-b">
          <div className="font-bold"> </div>
          <div className="text-xs text-gray-500">    </div>
          <div className="mt-2 text-xs bg-gray-50 border border-gray-200 rounded p-2">
            <div><span className="text-gray-500">:</span> <span className="font-medium">{row.guest_name}</span></div>
            <div><span className="text-gray-500">:</span> <span className="font-medium">{row.room_label}</span>  <span className="font-medium">{row.room_type_name}</span></div>
            <div><span className="text-gray-500">:</span> <span className="font-medium">{row.check_in_date}</span>  <span className="font-medium">{row.check_out_date}</span> (<span className="font-medium">{row.nights}</span> )</div>
            <div><span className="text-gray-500">:</span> <span className="font-medium">{row.total_amount}</span>  <span className="text-gray-500">:</span> <span className="font-medium">{row.amount_paid}</span>  <span className="text-gray-500">:</span> <span className="font-medium">{row.remaining_amount}</span></div>
          </div>
        </div>
        <div className="p-4 flex flex-col gap-3">
          <label className="text-sm"></label>
          <input type="number" className="border rounded px-3 py-2" value={amount} onChange={e=>setAmount(e.target.value)} min={0} />
          <label className="text-sm"> </label>
          <select className="border rounded px-3 py-2" value={method} onChange={e=>setMethod(e.target.value)}>
            <option value="cash"></option>
            <option value="instapay"></option>
            <option value="etisalat_cash"> </option>
          </select>
        </div>
        <div className="px-4 py-3 border-t bg-gray-50 flex items-center justify-between">
          <button className="px-3 py-2 border rounded" onClick={onClose}></button>
          <button className="px-3 py-2 rounded bg-emerald-600 text-white" disabled={loading} onClick={submit}>{loading?'...':''}</button>
        </div>
      </div>
    </div>
  );
}
