import React from 'react';

export default function ReservationCard({ r, onEdit, onDelete, onExtend, onPay, onInvoice }) {
  const cardAccent = (s) => (
    s==='confirmed' ? 'border-l-4 border-green-400 hover:bg-green-50' :
    s==='checked_in' ? 'border-l-4 border-blue-400 hover:bg-blue-50' :
    s==='checked_out' ? 'border-l-4 border-gray-400 hover:bg-gray-50' :
    s==='cancelled' ? 'border-l-4 border-red-400 hover:bg-red-50' :
    s==='no_show' ? 'border-l-4 border-amber-400 hover:bg-amber-50' : 'border-l-4 border-yellow-400 hover:bg-yellow-50'
  );
  const statusLabel = (s) => (
    s==='pending' ? ' ' :
    s==='confirmed' ? '' :
    s==='checked_in' ? ' ' :
    s==='checked_out' ? ' ' :
    s==='cancelled' ? '' :
    s==='no_show' ? ' ' : s
  );
  const statusClass = (s) => (
    s==='confirmed' ? 'bg-green-100 text-green-700' :
    s==='checked_in' ? 'bg-blue-100 text-blue-700' :
    s==='checked_out' ? 'bg-gray-100 text-gray-700' :
    s==='cancelled' ? 'bg-red-100 text-red-700' :
    s==='no_show' ? 'bg-orange-100 text-orange-700' : 'bg-yellow-100 text-yellow-700'
  );

  return (
    <div className={`bg-white rounded-lg shadow-sm border overflow-hidden transition-colors ${cardAccent(r.status)}`} dir="rtl">
      <div className={
        r.status==='confirmed' ? 'h-1 bg-green-400' :
        r.status==='checked_in' ? 'h-1 bg-blue-400' :
        r.status==='checked_out' ? 'h-1 bg-gray-400' :
        r.status==='cancelled' ? 'h-1 bg-red-400' :
        r.status==='no_show' ? 'h-1 bg-amber-400' : 'h-1 bg-yellow-400'
      } />
      <div className="p-4 flex flex-col gap-3">
        <div className="flex items-center justify-between">
          <div>
            <div className="font-semibold">{r.guest_name}</div>
            <div className="text-xs text-gray-500">{r.guest_phone || ''}{r.guest_phone && r.guest_email ? '  ' : ''}{r.guest_email || ''}</div>
          </div>
          <span className={`px-2 py-1 rounded text-xs ${statusClass(r.status)}`}>{statusLabel(r.status)}</span>
        </div>

        <div className="grid grid-cols-2 gap-x-4 gap-y-2 text-sm">
          <div><span className="text-gray-500">:</span> <span className="font-medium">{r.room_label || r.room_id}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.room_type_name || '-'}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.room_status || '-'}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium">{r.room_cleanliness || '-'}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium">{r.check_in_date}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium">{r.check_out_date}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.nights}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.guests_count || 0}</span></div>

          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.nightly_rate} {r.currency || ''}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.payment_method || '-'}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium">{r.total_amount ?? '-'}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium text-blue-700">{r.amount_paid ?? '-'}</span></div>
          <div><span className="text-gray-500">:</span> <span className="font-medium text-red-700">{r.remaining_amount ?? 0}</span></div>
          <div><span className="text-gray-500"> :</span> <span className="font-medium">{r.is_current ? '' : (r.is_upcoming ? '' : '')}</span></div>

          <div className="col-span-2"><span className="text-gray-500">  :</span> <span className="font-medium">{r.payer_type || '-'} {r.agency_name ? `(${r.agency_name})` : ''}</span></div>
          {r.special_requests && <div className="col-span-2 text-amber-700 bg-amber-50 border border-amber-200 rounded px-2 py-1"> : {r.special_requests}</div>}
          {r.notes && <div className="col-span-2 text-gray-700 bg-gray-50 border border-gray-200 rounded px-2 py-1">: {r.notes}</div>}
        </div>

        <div className="flex flex-wrap items-center justify-end gap-2 pt-1">
          <button className="px-3 py-1 border rounded text-xs" onClick={()=>onEdit(r)}></button>
          <button className="px-3 py-1 border rounded text-xs" onClick={()=>onExtend && onExtend(r)}></button>
          <button className="px-3 py-1 border rounded text-xs" onClick={()=>onInvoice && onInvoice(r)}></button>
          <button className="px-3 py-1 border rounded text-xs bg-emerald-600 text-white hover:bg-emerald-700" onClick={()=>onPay && onPay(r)}></button>
          <button className="px-3 py-1 border rounded text-xs text-red-700" onClick={()=>onDelete(r)}></button>
        </div>
      </div>
    </div>
  );
}
