import React from 'react';

export default function ReservationTable({ rows, loading, onEdit, onDelete }) {
  if (loading) return <div className="py-16 text-center text-gray-500">... </div>;
  if (!rows?.length) return <div className="py-16 text-center text-gray-500">   </div>;

  return (
    <div className="overflow-x-auto border rounded">
      <table className="min-w-full text-sm">
        <thead className="bg-gray-50">
          <tr className="text-right">
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
          </tr>
        </thead>
        <tbody>
          {rows.map(r => (
            <tr key={r.id} className="border-t">
              <td className="px-3 py-2 whitespace-nowrap">{r.guest_name}</td>
              <td className="px-3 py-2">{r.room_id}</td>
              <td className="px-3 py-2">{r.check_in_date}</td>
              <td className="px-3 py-2">{r.check_out_date}</td>
              <td className="px-3 py-2">{r.nights}</td>
              <td className="px-3 py-2">
                <span className={`px-2 py-1 rounded text-xs ${
                  r.status==='confirmed' ? 'bg-green-100 text-green-700' :
                  r.status==='checked_in' ? 'bg-blue-100 text-blue-700' :
                  r.status==='checked_out' ? 'bg-gray-100 text-gray-700' :
                  r.status==='cancelled' ? 'bg-red-100 text-red-700' :
                  r.status==='no_show' ? 'bg-orange-100 text-orange-700' : 'bg-yellow-100 text-yellow-700'
                }`}>{
                  r.status==='pending' ? ' ' :
                  r.status==='confirmed' ? '' :
                  r.status==='checked_in' ? ' ' :
                  r.status==='checked_out' ? ' ' :
                  r.status==='cancelled' ? '' :
                  r.status==='no_show' ? ' ' : r.status
                }</span>
              </td>
              <td className="px-3 py-2">{r.total_amount ?? '-'}</td>
              <td className="px-3 py-2">{r.amount_paid ?? '-'}</td>
              <td className="px-3 py-2">{(r.remaining_amount ?? 0)}</td>
              <td className="px-3 py-2">
                <div className="flex items-center gap-2">
                  <button className="px-2 py-1 border rounded text-xs hover:bg-gray-50" onClick={()=>onEdit(r)}></button>
                  <button className="px-2 py-1 border rounded text-xs text-red-700 hover:bg-red-50" onClick={()=>onDelete(r)}></button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
