import React, { useEffect, useMemo, useState } from 'react';
import { supabase } from '../supabaseClient';
import ReservationModal from '../components/ReservationModal';
import ReservationTable from '../components/ReservationTable';
import ReservationCard from '../components/ReservationCard';
import ExtendModal from '../components/ExtendModal';
import PaymentModal from '../components/PaymentModal';
import InvoiceModal from '../components/InvoiceModal';

export default function Reservations() {
  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [debounced, setDebounced] = useState('');
  const [page, setPage] = useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [totalCount, setTotalCount] = useState(0);
  const [filters, setFilters] = useState({ current: true, upcoming: false, inactive: false, status: '' });
  const [showModal, setShowModal] = useState(false);
  const [editing, setEditing] = useState(null);
  const [view, setView] = useState('cards'); // 'cards' | 'table'
  const [extendRow, setExtendRow] = useState(null);
  const [payRow, setPayRow] = useState(null);
  const [invoiceRow, setInvoiceRow] = useState(null);

  useEffect(() => { const t=setTimeout(()=>setDebounced(search),300); return ()=>clearTimeout(t); }, [search]);

  const buildQuery = () => {
    const q = supabase
      .from('reservations_overview')
      .select('*', { count: 'exact' })
      .order('check_in_date', { ascending: true, nullsFirst: false });
    const term = (debounced||'').trim();
    if (term) {
      q.or(`guest_name.ilike.%${term}%,room_id.eq.${term}`);
    }
    if (filters.current) q.eq('is_current', true);
    if (filters.upcoming) q.eq('is_upcoming', true);
    if (filters.inactive) q.eq('is_past', true);
    if (filters.status) q.eq('status', filters.status);
    const from = page * pageSize, to = from + pageSize - 1; q.range(from, to);
    return q;
  };

  const load = async () => {
    setLoading(true);
    try {
      const { data, error, count } = await buildQuery();
      if (error) throw error;
      setRows(data||[]); setTotalCount(count||0);
    } catch(e) { console.error('Load reservations failed', e); setRows([]); setTotalCount(0); }
    finally { setLoading(false); }
  };

  useEffect(()=>{ load(); }, [debounced, filters, page, pageSize]);

  // Realtime: auto-refresh on reservations changes
  useEffect(() => {
    const channel = supabase.channel('reservations-live')
      .on('postgres_changes', { event: '*', schema: 'public', table: 'reservations' }, () => {
        load();
      })
      .subscribe();
    return () => { try { supabase.removeChannel(channel); } catch(_) {} };
  }, []);

  const counters = useMemo(() => {
    const cur = rows.filter(r=>r.is_current).length;
    const upc = rows.filter(r=>r.is_upcoming).length;
    const past = rows.filter(r=>r.is_past).length;
    const tot = totalCount;
    return { cur, upc, past, tot };
  }, [rows, totalCount]);

  const openCreate = () => { setEditing(null); setShowModal(true); };
  const openEdit = (row) => { setEditing(row); setShowModal(true); };
  const handleExtend = (row) => { setExtendRow(row); };

  const handlePay = (row) => { setPayRow(row); };

  const handleInvoice = (row) => { setInvoiceRow(row); };

  const handleSave = async (payload) => {
    try {
      const cleaned = { ...payload };
      const pendingExtras = Array.isArray(cleaned.pending_guest_ids) ? cleaned.pending_guest_ids : [];
      delete cleaned.pending_guest_ids;
      if (editing && editing.id) {
        const { error } = await supabase.from('reservations').update(cleaned).eq('id', editing.id);
        if (error) throw error;
        // attach extra guests if provided
        for (const gid of pendingExtras) {
          try { await supabase.rpc('add_reservation_guest', { p_reservation_id: editing.id, p_guest_id: gid, p_role: 'additional' }); } catch(_) {}
        }
      } else {
        const { data, error } = await supabase.from('reservations').insert([cleaned]).select('id').single();
        if (error) throw error;
        const newId = data?.id;
        // attach extra guests to the new reservation if any
        if (newId && pendingExtras.length > 0) {
          for (const gid of pendingExtras) {
            try { await supabase.rpc('add_reservation_guest', { p_reservation_id: newId, p_guest_id: gid, p_role: 'additional' }); } catch(_) {}
          }
        }
      }
      setShowModal(false); await load();
    } catch(e) {
      console.error('Save reservation failed', e);
      const msg = String(e?.message || e || '').toLowerCase();
      let userMsg = '  .';
      if (msg.includes('overlapping reservation')) userMsg = ' :         .';
      else if (msg.includes('check_out_date must be after check_in_date')) userMsg = ' :        .';
      else if (msg.includes('row-level security')) userMsg = ' :      (RLS).';
      else if (msg.includes('not-null')) userMsg = ' :     .';
      else if (msg.includes('invalid input syntax')) userMsg = ' :     .';
      alert(userMsg);
    }
  };

  const handleDelete = async (row) => {
    try {
      const ok = window.confirm('  ');
      if (!ok) return;
      const { error } = await supabase.from('reservations').delete().eq('id', row.id);
      if (error) throw error;
      await load();
    } catch(e) { console.error('Delete reservation failed', e); alert('  : '+(e.message||e)); }
  };

  return (
    <div className="p-8" dir="rtl">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-2xl font-bold">  </h2>
          <p className="text-sm text-gray-500">     </p>
        </div>
        <button onClick={openCreate} className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded">  +</button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div className="bg-purple-50 border border-purple-200 rounded p-4 text-center">
          <div className="text-sm text-purple-700"> </div>
          <div className="text-2xl font-bold text-purple-700">{counters.cur}</div>
        </div>
        <div className="bg-amber-50 border border-amber-200 rounded p-4 text-center">
          <div className="text-sm text-amber-700"></div>
          <div className="text-2xl font-bold text-amber-700">{counters.upc}</div>
        </div>
        <div className="bg-gray-50 border border-gray-200 rounded p-4 text-center">
          <div className="text-sm text-gray-700">  / </div>
          <div className="text-2xl font-bold text-gray-700">{counters.past}</div>
        </div>
      </div>

      <div className="flex flex-wrap items-center gap-3 mb-4">
        <div className="relative flex-1 min-w-[220px]">
          <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400">
            <svg className="w-4 h-4" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2"><circle cx="11" cy="11" r="7"/><path d="M21 21l-4.3-4.3"/></svg>
          </span>
          <input className="border rounded pl-9 pr-3 py-2 w-full" placeholder="    " value={search} onChange={e=>{ setSearch(e.target.value); setPage(0); }} />
        </div>
        <button onClick={()=>{ setFilters(s=>({ ...s, current: !s.current })); setPage(0); }}
          className={`px-3 py-2 rounded text-sm border ${filters.current ? 'bg-purple-600 text-white border-purple-600' : 'bg-white text-gray-700 hover:bg-gray-50'}`}></button>
        <button onClick={()=>{ setFilters(s=>({ ...s, upcoming: !s.upcoming })); setPage(0); }}
          className={`px-3 py-2 rounded text-sm border ${filters.upcoming ? 'bg-amber-600 text-white border-amber-600' : 'bg-white text-gray-700 hover:bg-gray-50'}`}></button>
        <button onClick={()=>{ setFilters(s=>({ ...s, inactive: !s.inactive })); setPage(0); }}
          className={`px-3 py-2 rounded text-sm border ${filters.inactive ? 'bg-gray-700 text-white border-gray-700' : 'bg-white text-gray-700 hover:bg-gray-50'}`}> </button>
        <select className="border rounded px-3 py-2 text-sm" value={filters.status} onChange={e=>{ setFilters(s=>({ ...s, status: e.target.value })); setPage(0); }}>
          <option value=""> </option>
          <option value="pending"> </option>
          <option value="confirmed"></option>
          <option value="checked_in"> </option>
          <option value="checked_out"> </option>
          <option value="cancelled"></option>
          <option value="no_show"> </option>
        </select>
        <div className="ml-auto flex items-center gap-2">
          <button className={`px-3 py-2 rounded text-sm border ${view==='cards'?'bg-gray-800 text-white':'bg-white text-gray-700'}`} onClick={()=>setView('cards')}> </button>
          <button className={`px-3 py-2 rounded text-sm border ${view==='table'?'bg-gray-800 text-white':'bg-white text-gray-700'}`} onClick={()=>setView('table')}> </button>
        </div>
      </div>

      {view==='table' ? (
        <ReservationTable rows={rows} loading={loading} onEdit={openEdit} onDelete={handleDelete} />
      ) : (
        loading ? (
          <div className="py-16 text-center text-gray-500">... </div>
        ) : rows.length === 0 ? (
          <div className="py-16 text-center text-gray-500">   </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-4">
            {rows.map(r => (
              <ReservationCard key={r.id} r={r} onEdit={openEdit} onDelete={handleDelete} onExtend={handleExtend} onPay={handlePay} onInvoice={handleInvoice} />
            ))}
          </div>
        )
      )}

      <div className="flex items-center justify-between mt-6">
        <div className="text-sm text-gray-600">: {rows.length} / : {totalCount}</div>
        <div className="flex items-center gap-2">
          <button className="px-3 py-1 border rounded" onClick={()=>setPage(p=>Math.max(0,p-1))} disabled={page===0}></button>
          <span className="text-sm"> {page+1}</span>
          <button className="px-3 py-1 border rounded" onClick={()=>setPage(p=>p+1)} disabled={(page+1)*pageSize>=totalCount}></button>
          <select className="border rounded px-2 py-1" value={pageSize} onChange={e=>{ setPageSize(Number(e.target.value)); setPage(0); }}>
            <option value={10}>10</option>
            <option value={20}>20</option>
          </select>
        </div>
      </div>

      {showModal && (
        <ReservationModal
          initialData={editing}
          onClose={()=>setShowModal(false)}
          onSave={handleSave}
        />
      )}
      {extendRow && (
        <ExtendModal row={extendRow} onClose={()=>setExtendRow(null)} onDone={load} />
      )}
      {payRow && (
        <PaymentModal row={payRow} onClose={()=>setPayRow(null)} onDone={load} />
      )}
      {invoiceRow && (
        <InvoiceModal row={invoiceRow} onClose={()=>setInvoiceRow(null)} />
      )}
    </div>
  );
}
